<template>
  <div class="container casino pt-4">
    <div class="columns">
      <div class="column content is-medium">
        <h1 class="white-text">Testing Web3 - MetaMask</h1>
        <img src="https://alpha.emdx.io/img/logo.png" alt="" />
      </div>
    </div>
    <div v-if="network != 'Kovan test network'">
      <div class="column content is-small box m-4 p-6">
        <div v-if="!isInjected" class="pb-4">
          <h2>Download and connect MetaMask</h2>
          <button class="button is-primary" @click="activateMetaMask()">
            Connect MetaMask
          </button>
        </div>
        <h2 class="mb-0">Switch to "Kovan Test Network"</h2>
      </div>
    </div>
    <div v-else-if="!isCheckingTransaction && !transactionDone" class="columns">
      <div class="column content is-small box m-4 p-6">
        <h2>Send from</h2>
        <b-field class="pt-2" label="Address">
          <b-input
            v-model="coinbase"
            placeholder="Address"
            icon="account"
            disabled
          >
          </b-input>
        </b-field>
        <b-field class="pt-2" label="Amount">
          <b-input
            v-model.number="sendFromAmount"
            placeholder="Amount"
            icon="account"
          >
          </b-input>
        </b-field>
        <p class="mb-5">
          Current balance: <strong> {{ ethBalance }} </strong> ETH
        </p>
        <p class="mb-5">
          Balance after transfer:
          <strong> {{ ethBalance - sendFromAmount }} </strong> ETH
        </p>
        <button
          :class="
            pending != true
              ? 'button is-primary'
              : 'button is-primary is-loading'
          "
          @click="clickNumber"
        >
          Transferir con MetaMask
        </button>
      </div>
      <!--       <div class="column content is-small box m-4">
        <h2>Send to</h2>
        <b-field class="pt-2" label="Address">
          <b-input v-model="amount" placeholder="Address" icon="account">
          </b-input>
        </b-field>
      </div> -->
    </div>
    <div v-else-if="!isCheckingTransaction && transactionDone" class="columns">
      <div class="column content is-small box m-4 p-6">
        <div class="column"><h2>Transaction Successful</h2></div>
        <button class="button is-primary" @click="transactionDone = false">Go back</button>
      </div>
    </div>
    <div v-else-if="isCheckingTransaction && transactionDone" class="columns">
      <div class="column content is-small box m-4 p-6">
        <div class="column">
          <h2>Checking transaction</h2>
          <h3>Please wait...</h3>
          <img height="50" src="https://mir-s3-cdn-cf.behance.net/project_modules/disp/dae67631234507.564a1d230a290.gif" alt="">
        </div>
      </div>
    </div>
    
  </div>
</template>

<script>
import { NETWORKS } from "../util/constants/networks";
import { mapState } from "vuex";
export default {
  name: "casino",
  data() {
    return {
      amount: null,
      pending: false,
      winEvent: null,

      // New variables
      sendToAmount: null,
      sendToAddress: null,
      sendFromAmount: 0.000345,
      sendFromAddress: null,
      transactionDone: false,
    };
  },
  computed: mapState({
    coinbase: (state) => state.web3.coinbase,
    isCheckingTransaction: (state) => state.web3.isCheckingTransaction,
    ethBalance: (state) => {
      if (state.web3.web3Instance !== null)
        return state.web3.web3Instance().fromWei(state.web3.balance, "ether");
    },
    network: (state) => NETWORKS[state.web3.networkId],
    isInjected: (state) => state.web3.isInjected,
  }),
  methods: {
    clickNumber() {
      console.log("Se van a transferir: ", this.sendFromAmount);
      this.winEvent = null;
      this.pending = true;
      this.$store.state.contractInstance().bet(
        1,
        {
          gas: 300000,
          value: this.$store.state.web3
            .web3Instance()
            .toWei(this.sendFromAmount, "ether"),
          from: this.$store.state.web3.coinbase,
        },
        (err, result) => {
          if (err) {
            console.log(err);
            this.pending = false;
          } else {
            this.$store.state.web3.isCheckingTransaction = true;
            this.transactionDone = true;
            console.log("Empezando a transferir");
            this.pending = false;
            let Won = this.$store.state.contractInstance().Won();
            Won.watch((err, result) => {
              if (err) {
                this.pending = false;
                console.log("could not get event Won()");
              } else {
                this.pending = false;
                console.log("Entro todo bien, hay que modificar");
                this.winEvent = result.args;
                this.winEvent._amount = parseInt(result.args._amount, 10);
              }
            });
          }
        }
      );
    },
    activateMetaMask() {
      window.ethereum.enable();
    },
  },
  mounted() {
    console.log("dispatching getContractInstance");
    this.$store.dispatch("getContractInstance");
  },
};
</script>

<style scoped>
.casino {
  color: white;
  background: #050e18;
  text-align: center;
}
#loader {
  width: 150px;
}
ul {
  margin: 25px;
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-column-gap: 25px;
  grid-row-gap: 25px;
}
li {
  padding: 20px;
  margin-right: 5px;
  border-radius: 50%;
  cursor: pointer;
  background-color: #fff;
  border: -2px solid #bf0d9b;
  color: #bf0d9b;
  box-shadow: 3px 5px #bf0d9b;
}
li:hover {
  background-color: #bf0d9b;
  color: white;
  box-shadow: 0px 0px #bf0d9b;
}
li:active {
  opacity: 0.7;
}
* {
  color: #444444;
}
#has-won {
  color: green;
}
#has-lost {
  color: red;
}
.white-text {
  color: white;
}
</style>



